<template>
  <b-container>
    <div class="hello">
      <h1>{{ msg }}</h1>
    </div>
    <div>
      <b-overlay :show="show" rounded="sm" @shown="onShown" @hidden="onHidden">
        <div>
          <b-form-input
            ref="descriptionInput"
            v-model="description"
            v-on:keyup.enter = "nameCheck"
            placeholder="HGVS description"
            autofocus></b-form-input>
        </div>
        <div class="examples-list">
          Examples:
          <code
            v-for="(example, index) in examples"
            :key="index"
            @click.prevent="selectExample(index)"
          >
            <span class="example-item">{{ example }}</span>
          </code>
        </div>
        <div>
          <b-button
            ref="nameCheck"
            @click="nameCheck"
            block variant="primary"
          >
            Normalize
          </b-button>
          <!-- <b-button
            ref="getDescriptionModel"
            @click="getDescriptionModel"
            block variant="primary"
          >
            Get Description Model
          </b-button>
          <b-button v-if="descriptionModel"
            ref="getReferenceModel"
            @click="getReferenceModel"
            block variant="primary"
          >
            Get Reference Model
          </b-button> -->
        </div>
        <template v-slot:overlay>
          <div class="text-center">
            <b-icon icon="stopwatch" font-scale="3" animation="cylon"></b-icon>
            <p id="cancel-label">Please wait...</p>
            <b-button
              ref="cancel"
              variant="outline-danger"
              size="sm"
              aria-describedby="cancel-label"
              @click="cancel"
            >
            Cancel
          </b-button>
        </div>
      </template>
      </b-overlay>
    </div>
    <div v-if="errors">
      <div>
        <hr>
        <h4>Errors</h4>
        {{ errors }}
      </div>
    </div>
    <div v-if="summary">
      <div>
        <hr>
        <h4>Normalized Description</h4>
        <code><span class="normalized-description">{{ normalizedDescription }}</span></code>
      </div>
      <div v-if="visualize && referenceModel">
        <hr>
        <h4>Operations Visualization</h4>
        <div>
          <DetailVariants
            :visualize="visualize"
            :sequence="sequence"
          />
        </div>
      </div>
      <div v-if="equivalentDescriptions">
        <hr>
        <h4>Equivalent Descriptions</h4>
        <div class="examples-list">
          <p v-for="description in equivalentDescriptions" :key="description">
            <code><span class="normalized-description">{{ description }}</span></code>
          </p>
        </div>
      </div>
      <div v-if="proteinDescriptions">
        <hr>
        <h4>Protein Descriptions</h4>
        <div class="examples-list">
          <p v-for="description in proteinDescriptions" :key="description">
            <code><span class="normalized-description">{{ description }}</span></code>
          </p>
        </div>
      </div>
      <b-button v-b-toggle.collapse-1 variant="primary">Show JSON</b-button>
      <b-collapse id="collapse-1" class="mt-2">
        <b-card>
          <div v-if="Object.keys(summary).length !== 0">
            <JsonPretty :summary="summary"/>
          </div>
        </b-card>
      </b-collapse>
    </div>
    <div v-if="descriptionModel">
      <div>
        <hr>
        <h4>Description Model Obtained</h4>
      </div>
      <b-button v-b-toggle.collapse-2 variant="primary">Show JSON</b-button>
      <b-collapse id="collapse-2" class="mt-2">
        <b-card>
          <div v-if="Object.keys(descriptionModel).length !== 0">
            <JsonPretty :summary="descriptionModel"/>
          </div>
        </b-card>
      </b-collapse>
    </div>
    <div v-if="referenceModel">
      <div>
        <hr>
        <h4>Reference Model Obtained</h4>
      </div>
      <b-button v-b-toggle.collapse-3 variant="primary">Show JSON</b-button>
      <b-collapse id="collapse-3" class="mt-2">
        <b-card>
          <div v-if="Object.keys(referenceModel).length !== 0">
            <JsonPretty :summary="referenceModel"/>
          </div>
        </b-card>
      </b-collapse>
    </div>
    <hr>
  </b-container>
</template>

<script>
import axios from 'axios'
const CancelToken = axios.CancelToken;
let cancel;

import JsonPretty from './JsonPretty.vue'
import DetailVariants from './DetailVariants.vue'

export default {
  name: 'NameCheck',
  components: {
    JsonPretty,
    DetailVariants
  },
  props: {
    msg: String
  },
  data() {
    return {
      description: null,
      descriptionModel: null,
      referenceModel: null,
      normalizedDescription: null,
      equivalentDescriptions: null,
      proteinDescriptions: null,
      visualize: null,
      sequence: null,
      summary: null,
      errors: null,
      show: false,
      examples: [
        'NG_012337.1:g.7125G>T',
        // 'NG_012337.1:g.10_11ins[T;10_20inv;NG_008835.1:159_170]',
        // 'NG_008835.1(NR_120672.1):n.159dup',
        // 'NG_012337.1(NM_003002.2):c.274G>T',
        // 'LRG_23:c.159dup',
        'LRG_24:g.5525_5530del',
        // 'LRG_24:c.159dup',
        // 'LRG_24(t3):c.159dup',
        // 'LRG_24(t1):c.159dup',
        // 'LRG_24:g.[5A>T;10_12del]',
        // 'LRG_24:g.[5A>T;13_15del]',
        // 'NC_000024.10:g.100del',
        // 'NG_008835.1:n.100del',
        // 'NM_003002.4:c.1del',
        // 'NR_120672.1:n.159dup'
      ],
    }
  },
  methods: {
    nameCheck: function () {
      if (this.description !== null) {
        this.show = true
        this.summary = null
        this.errors = null
        this.equivalentDescriptions = null
        this.proteinDescriptions = null
        this.visualize = null
        axios
          .get('http://145.88.35.44/api/name_check/' + this.description, {
            cancelToken: new CancelToken(function executor(c) {
            // An executor function receives a cancel function as a parameter
            cancel = c;
            })
          })
          .then(response => {
            if (response.data) {
              this.summary = response.data
              this.show = false
              this.normalizedDescription = response.data["normalized description"]
              if (response.data["errors"]) {
                this.errors = response.data["errors"]
              }
              if (response.data["equivalent descriptions"]) {
                this.equivalentDescriptions = response.data["equivalent descriptions"]
              }
              if (response.data["protein descriptions"]) {
                this.proteinDescriptions = response.data["protein descriptions"]
              }
              if (response.data["visualize"]) {
                this.visualize = response.data["visualize"]
              }
              console.log('tr')
              this.getDescriptionModel()
            }
          })
      }
      else {
        this.$refs.descriptionInput.focus()
      }
    },
    getDescriptionModel: function () {
      console.log(this.description)
      if (this.description !== '') {
        this.show = true
        axios
          .get('http://145.88.35.44/api/description_to_model/' + this.description, {
            cancelToken: new CancelToken(function executor(c) {
            // An executor function receives a cancel function as a parameter
            cancel = c;
            })
          })
          .then(response => {
            if (response.data) {
              this.descriptionModel = response.data
              this.show = false
              this.getReferenceModel()
            }
          })
      }
      else {
        this.$refs.descriptionInput.focus()
      }
    },
    getReferenceModel: function () {
      console.log(this.descriptionModel)
      if (this.descriptionModel) {
        let reference_id = this.descriptionModel["reference"]["id"]
        this.show = true
        this.sequence = null
        axios
          .get('http://145.88.35.44/api/reference_model/' + reference_id, {
            cancelToken: new CancelToken(function executor(c) {
            // An executor function receives a cancel function as a parameter
            cancel = c;
            })
          })
          .then(response => {
            if (response.data) {
              this.referenceModel = response.data
              this.sequence = response.data["sequence"]["seq"]
              this.show = false
            }
          })
      }
      else {
        this.$refs.descriptionInput.focus()
      }
    },
    selectExample: function(index) {
      this.description = this.examples[index]
      this.$refs.descriptionInput.focus()
    },
    onShown() {
       // Focus the cancel button when the overlay is showing
       this.$refs.cancel.focus()
     },
     onHidden() {
     },
     cancel () {
       cancel()
       this.show = false
     }
  }
}
</script>

